.TeamMember{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px;
}
.TeamMember a{
    text-decoration: none;
    color: black;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    background-color: tan;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 10px 10px 34px 0px rgba(74,71,74,1);
}

.Team-member-img img{
    border-radius: 50%;
    width: 250px;
    height: 250px;
}

.icon i{
    color: white;
    font-size: 2.5rem;
    margin: 0.5rem ;
    transition: 0.5s ease-in-out;
}

.icon i:hover{
    color: red;
}
