.SingleTeamMember{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80%;
    flex-wrap: wrap;
    margin: 0 auto;
}
.SingleTeamMember-skill{
    background-color: sandybrown;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 300px;
    height: 300px;
    margin: 1.5rem ;
    padding: 0 auto;
    box-shadow: 10px 10px 34px 0px rgba(74,71,74,1);

}
.SingleTeamMember-skill i{
    color: wheat;
    transition: 0.4s ease-in-out;
}
.SingleTeamMember-skill i:hover{
    color: white;
    transform: scale(1.1);
}

.Header{
    font-size: 5rem;
   text-align: center;
}
   